import gameData from '../data/game-data';
import getNumEnding from '../data/get-num-ending';
import getMinutes from '../data/get-minutes';

const mistakesCount = gameData.NOTES_COUNT - 1;
const gameTime = getMinutes(gameData.GAME_TIME);
let timeString = `${gameTime.min} ${getNumEnding(gameTime.min, [``, ``, ``])}`;
if (gameTime.sec) {
  timeString += ` ${gameTime.sec} ${getNumEnding(gameTime.sec, [``, ``, ``])}`;
}

export default {
  startText: ` `,
  content: {
    title: ` `,
    text: ` &nbsp; &nbsp;${timeString}    .<br>
        ${mistakesCount} ${getNumEnding(mistakesCount, [``, ``, ``])}.<br>
      !`
  }
};
